.select-filter{
    border-radius: 5px;
    border-width: 2px;
    border-color: #FF0742;
    color:#FF0742 ;
    width: 60px;
}
.Search{
    border-radius: 5px;
    border-width: 2px;
    color:#FF0742 ;
    border-color: #FF0742;
}
.jam{
    position:absolute;
    padding-left:20px;
    padding-right:20px;
    top:-20px;
    left:35%;
    border-radius:20px;
    background-color:#FF0742;
    color:#ffffff;
    height:200;
}
.jam h1{
    padding-top: 80px;
}
@media screen and (max-width:480px){
    .jam{
        position:absolute;
        padding-left:0px;
        padding-right:0px;
        top:-20px;
        left: 0%;
        border-radius:20px;
        background-color:#FF0742;
        color:#ffffff;
    }
    .jam h1{
        padding-top: 20px;
        font-size: 25px;
        padding-left: 19px;
        padding-right: 19px;
    }
}