.Home{
    
}
.Home img{
    width: 100%;
    margin-left: 0px;
    margin-right: 0px;
    height: 550px;
}
.Content{
    max-width: 2000px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr  ;
    gap: 380px;
    background-color:white;
    position: absolute;
    top: 470px;
    margin-left: -600px;
    height: 100px;
    border-radius: 10px;
    text-align: center;
    left:50%;
}
.Content-Text{
    position: absolute;
    top: 50px;
    left: 50%;
    margin-left: -230px;
}
.Content-Text h1{
    font-size: 50px;
    text-align: center;
    position: relative;
    color: white;
}
@media screen and (max-width:480px){
    .Content{
        max-width: 480px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr  ;
        gap: 10px;
        background-color:white;
        position: absolute;
        top: 470px;
        margin-left: 0px;
        height: 100px;
        border-radius: 10px;
        text-align: center;
        left:0px;
    }
}